

.navbar {
    height: 50px;
    width: 100%;
    padding: 1rem; 
    border-bottom: 1px solid #b3b3b3;
    box-shadow: 0 4px 13px -3px rgba(0, 0, 0, 0.10196);
    background-color: rgb(248, 248, 248);
    font-family: "Google Sans", sans-serif;
}

.nav-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    width: 100%;
  }

  .navbar-brand {
    height: 80%;
    display: inline-block;
    margin: 5px 0.5rem;
  } 

  .navbar-collapse {
    margin: 10px;
     width: 50%;
     display: inline-block;
  } 

  .navbar-collapse ul {
    display: grid;
    grid-template-columns: 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
  } 
  
  .navbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .nav-item {
    list-style: none;
    margin: 0 10px;
    font-size: 20px;
  }
  
  .nav-item:hover{
    opacity: 0.7;
  }
  
  .navbar-toggler {
    height: 40px;
    width: 50px;
    border-radius: 10px;
    float: right;
    margin: 5px;
    cursor: pointer;
    display: none;
    border: none;
  }
  
  .navbar-toggler-icon {
    display: inline-block;
    height: 30px;
    width: 30px;
    vertical-align: middle;
    background-image: url("black.png");
    background-repeat: no-repeat;
    background-size: contain;
    opacity: 0.7;
  }

  .nav-container a{
    text-decoration: none;
  }
  
  @media screen and (max-width: 970px) {

    .navbar-toggler{
        display: block;
    }

    .navbar-collapse ul {
        margin: 5px 10px;
        display: grid;
        grid-template-columns: repeat(4, 2.5fr);
    }

    .nav-item{
        height: 40px;
    }

    /* .navbar-collapse {
        display: none;
    } */

    /* .navbar{
        height: 130px;
    } */

}
  
  @media screen and (max-width: 500px) {

    .navbar-collapse ul {
        display: grid;
        grid-template-columns: repeat(2, 5fr);
    }

    /* .navbar{
        height: 210px;
    } */

}

.news-container{
  width: 100vw;
}

.news-container h1{
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Fjalla One', sans-serif;
  font-weight: 800;
  margin: 30px;
}

.row{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.col{
  margin: 20px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

  .card {
    width: 20rem;
    border: 1px solid rgb(141, 134, 134);
  }

  .Source {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #000;
    background-color: #ff3f3f;
  }

  .Source strong{
      letter-spacing: 1px;
      color: antiquewhite;

  }

.card img{
  height: 179px;
  width: 100%;
}

.card-body{
  padding: 10px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}

.link{
  display: flex;
  justify-content: center;
  align-items: center;
}

.link a{
  margin-top: 20px;
  width: 150px;
  height: 40px;
  background-color: #4e6af6;
  border-radius: 20px;
  text-decoration: none;
  color: antiquewhite;
  text-align: center;
  padding-top: 8px;
}

@media screen and (max-width: 1100px) {

  .row{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .card {
    width: 25rem;
  }

}
@media screen and (max-width: 890px) {

  .row{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  /* .card {
    max-width: 28rem;
  } */

}

.btn{
  height: 50px;
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.btn button{
  margin: 10px 50px;
  width: 100px;
  height: 30px;
  background-color: #494f4f;
  color: antiquewhite;
  border: 2px solid black;
  border-radius: 20px;
  cursor: pointer;
  text-align: center;
}

.btn button:hover{
  opacity: 0.8;
}

.spin{
  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.card-author {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
